  margin-bottom: 20px;
}
.wysiwyg h1 {
  font-size: 3.2rem;
  line-height: 1.3;
  font-weight: 700;
}
.wysiwyg h2 {
  font-size: 2.8rem;
  line-height: 1.4;
  font-weight: 700;
}
.wysiwyg h3 {
  font-size: 2.1rem;
