  font-weight: 700;
}
.wysiwyg em {
  font-style: italic;
}
.wysiwyg ul {
  list-style: none;
  padding-left: 24px;
  margin: 30px 0 20px;
}
.wysiwyg ul li {
  margin-bottom: 4px;
  position: relative;
}
.wysiwyg ul li:before {
  content: "";
  border-radius: 50%;
  height: 8px;
  width: 8px;
  background: #194777;
  position: absolute;
  top: 50%;
  left: -24px;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
}
.wysiwyg ol {
  list-style: none;
  counter-reset: counter;
  padding-left: 24px;
  margin: 30px 0 20px;
}
.wysiwyg ol li {
  counter-increment: counter;
  position: relative;
  margin-bottom: 4px;
}
.wysiwyg ol li:before {
  position: absolute;
  top: 50%;
  left: -24px;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  content: counter(counter) ". ";
  color: #194777;
  font-weight: 600;
}
.wysiwyg ol li ul {
  margin: 8px 0 8px 20px;
}

