  transition: 0.25s ease-in-out;
}
.menu-toggle span {
  background: #000000;
  display: block;
}
.menu-toggle .hamburger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0;
